</script>

<style lang="stylus" scoped>

  @import '~@/style/skewd_bg.styl'

  .head_main
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;

    .tabs
      display: flex;
      width: 60%;

      padding-left: 24px;
      padding-right: 24px;
      justify-content: center;

    .top_center

      display flex
      flex 1
      justify-content center

    .top_right_main
      display: flex;
      width: 20%;
      align-items: center;
      justify-content: center


  .el-header {
    line-height: 60px;

    display: flex;
    width 100%

  }

  .el-container {
    display flex;
    flex-direction column;
    align-items center;

  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    width 75%
  }

  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;

  }

  div img {
    cursor: pointer;
    transition: all 0.6s;
  }

  div img:hover {

    -webkit-transform: rotate(360deg) scale(1.6); /*兼容Chrome*/

    -moz-transform: rotate(360deg) scale(1.6); /*兼容Firefox*/

    -ms-transform: rotate(360deg) scale(1.6); /*兼容IE*/

    -o-transform: rotate(360deg) scale(1.6); /*兼容Opera*/
  }


</style>



