<template>
  <div>


      <DataCard :showNumber="3" :objs="temp_obj" />

  </div>
</template>

<script>


    import DataCard from "./DataCard";


    export default {
        name: "Tab0",
        components: {DataCard},

        data() {
            return {

                temp_obj:[],
                sql_pic: this.Common_Base.SQL_Pic,
                tag_filter: this.Common_Base.tags[0]
            }
        },

        mounted() {
            this.getShowDatas()
        },
        methods: {
            getShowDatas() {

                const query = Bmob.Query(this.sql_pic);

                query.find().then(res => {
                    // console.log(res)

                    res=res.reverse()
                    let temp_obj=[];

                    for (let item of res) {
                        let urls = item.url.split(",")
                        temp_obj.push({'urls': urls,
                            'description': item.description,
                            'url0': urls[0],
                            "tags":item.tags
                        });

                    }

                    this.temp_obj = Object.assign([], this.temp_obj, temp_obj)

                    this.Common_Base.data_alls= this.temp_obj;
                    // console.log("123")
                    // console.log(this.Common_Base.data_alls)


                }).catch(error => {
                    console.log(error)
                });

                // this.axios.get('https://bing.getlove.cn/latelyBingImageStory')
                //   .then(data => {
                //
                //   }).catch(error => {
                //   console.log(error)
                // })
            }
        }
    }
</script>

<style scoped>

</style>



