<template>

  <div class="main">
    <el-button @click="updateAction">保存更新</el-button>
    <div v-for="(item, index) of list_datas" class="main_list" v-viewer>
      <PicDataMotify :obj="item" @click_base="pic_update"
                     @click_tag_add="tag_add"
                     @click_tag_remove="tag_remove"

      />
    </div>
  </div>
</template>

<script>

    import PicDataMotify from "./PicDataMotify";


    export default {
        name: "ManageMotify",
        components: {PicDataMotify},
        data() {
            return {
                list_datas: [],
                sql_pic: this.Common_Base.SQL_Pic
            }
        },
        mounted() {
            const query = Bmob.Query(this.sql_pic);
            query.find().then(res => {
                // console.log(res)
              res=res.reverse()
                for (let item of res) {
                    let urls = item.url.split(",")
                    let tags0=[]

                    if(typeof(item.tags)!='undefined'){
                        if(item.tags!=''){
                            tags0=item.tags.split(",")
                        }
                    }

                    this.list_datas.push({
                            'urls': urls,
                            'description': item.description,
                            'url0': urls[0],
                            'tags0':tags0
                        }
                    );

                    // this.list_datas.push({'url': item.url, 'text': item.description})
                }

            }).catch(error => {
                console.log(error)
            })
        },
        methods: {
            updateAction() {
                const query = Bmob.Query(this.sql_pic);
                query.find().then(todos => {
                    todos.destroyAll().then(res => {
                        // 成功批量修改
                        console.log(res, 'ok')

                        this.addResultDatas();

                    }).catch(err => {
                        console.log(err)
                    });
                })
            },
            addResultDatas() {
                const queryArray = new Array();
                // 构造含有50个对象的数组
                for (var i = 0; i < this.list_datas.length; i++) {
                    var queryObj = Bmob.Query(this.sql_pic);

                    queryObj.set('url', this.list_datas[i].urls.join(","));
                    queryObj.set('tags', this.list_datas[i].tags0.join(","));
                    queryObj.set('description', this.list_datas[i].description);

                    queryArray.push(queryObj);
                }

                // 传入刚刚构造的数组
                Bmob.Query(this.sql_pic).saveAll(queryArray).then(result => {
                    console.log(result);
                    this.loading = false
                    this.$message('修改成功,去首页看看最新商品吧');
                }).catch(err => {

                    this.$message(err);
                    this.loading = false
                });
            },
            pic_update(obj) {
                console.log(obj)
                this.list_datas.remove(obj)
            },
            tag_add(obj,tag) {
                let index = this.list_datas.indexOf(obj);
                if(index<=-1){
                    console.log('tag_add index error')
                    return
                }
                let real_obj=this.list_datas[index];

                if (real_obj.tags0.indexOf(tag) > -1) {
                    console.log('已存在' + tag)
                    return
                }
                real_obj.tags0.push(tag)
            },
            tag_remove(obj,tag) {
                let index = this.list_datas.indexOf(obj);
                if(index<=-1){
                    console.log('tag_remove index error')
                    return
                }
                let real_obj=this.list_datas[index];
                real_obj.tags0.remove(tag)
            },
        }
    }
</script>

<style lang="stylus" scoped>

  .main
    display flex
    flex-direction column
    align-items center
    padding 1rem

    .main_list

      width 80%


</style>



