import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


import Index from "../components/home/Index";
import Tab1 from "../components/home/Tab1";
import Tab2 from "../components/home/Tab2";
import Tab3 from "../components/home/Tab3";
import Tab4 from "../components/home/Tab4";
import Tab0 from "../components/home/Tab0";


import Login from "../components/manager/Login";
import Tab6 from "../components/home/Tab6";
import Manager from "../components/manager/Manager";
import ManageMotify from "../components/manager/ManageMotify";
import ManagerAdd from "../components/manager/ManagerAdd";
import ContactTab from "../components/home/ContactTab";


const routes = [
  {
    path: '/',
    name: 'home',
    component: Index,
    children: [
      {
        path: '/type1',
        name: 'type1',
        component: Tab1,

      }, {
        path: '/type2',
        name: 'type2',
        component: Tab2,
      },
      {
        path: '/type3',
        name: 'type3',
        component: Tab3,
      },
      {
        path: '/type0',
        name: 'type0',
        component: Tab0,
      }, {
        path: '/type4',
        name: 'type4',
        component: Tab4,
      }, {
        path: '/five',
        name: 'five',
        component: ContactTab,
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/manager',
    name: 'manager',
    component: Manager,
    children: [
      {
        path: '/manager_add',
        name: 'manager_add',
        component: ManagerAdd,
      },
      {
        path: '/manager_motify',
        name: 'manager_motify',
        component: ManageMotify,
      }
    ]
  },
]

export default new Router({
  routes,
})



