<template>
  <div>
    <DataCard :showNumber="3" :objs="temp_obj" />
  </div>
</template>

<script>


    import DataCard from "./DataCard";


    export default {
        name: "Tab4",
        components: {DataCard},

        data() {
            return {
                temp_obj:[],
                sql_pic: this.Common_Base.SQL_Pic,
                tag_filter: this.Common_Base.tags[4]
            }
        },
        activated() {
            console.log("---------activated  TAB4-----")

            if(this.temp_obj.length===0){
                this.temp_obj = this.Common_Base.getFilterDatas(this.tag_filter, this.Common_Base.data_alls)
            }
        },

        created() {
            console.log("---------created  TAB4-----")
            this.temp_obj = this.Common_Base.getFilterDatas(this.tag_filter, this.Common_Base.data_alls)
        },
        mounted() {
            console.log("---------mounted  TAB1-----")
        },
        methods: {

        }
    }
</script>

<style scoped>

</style>



