<template>

</template>

<script>

    const tags = []
    tags.push('ALL')
    tags.push('NEW RELEASES')
    tags.push('YOGA')
    tags.push('SEAMLESS')
    tags.push('ACCESSORIES')

    const tags_add = []
    tags_add.push('NEW RELEASES')
    tags_add.push('YOGA')
    tags_add.push('SEAMLESS')
    tags_add.push('ACCESSORIES')

    const tags_router = []
    let index = 0
    for (let tag of tags) {
        tags_router.push({'name': tag, 'url': 'type' + index});
        index++;
    }

    let data_alls=[]

    const SQL_Pic='Pic_Data'

    function getFilterDatas(tag_filter,data_alls){
            let temp_objs = []
            for (let item of  data_alls) {
                if(typeof(item.tags)=='undefined' || item.tags==''){
                    continue
                }
                let tags0 = item.tags.split(",")
                let show=false
                if (tag_filter.toUpperCase() == 'ALL') {
                    show = true
                } else {
                    for (let tag of tags0) {
                        // console.log(tag)
                        if (tag.toUpperCase() == tag_filter) {
                            show = true
                            break
                        }
                    }
                }
                // console.log('   ' + show)
                if (!show) {
                    continue
                }
                temp_objs.push({'urls': item.urls,
                    'description': item.description,
                    'url0': item.url0,
                    "tags":item.tags
                });
            }

            // console.log('ddd')
            // console.log(temp_objs)
            return temp_objs
    }
    export default {
        name: "Common",
        tags,
        tags_add,
        SQL_Pic,
        tags_router,
        data_alls,
        getFilterDatas
    }
</script>

<style scoped>

</style>



