<template>
  <div>
    <DataCard :showNumber="3" :objs="temp_obj"/>
  </div>
</template>

<script>


    import DataCard from "./DataCard";


    export default {
        name: "Tab2",
        components: {DataCard},

        data() {
            return {
                temp_obj: [],
                sql_pic: this.Common_Base.SQL_Pic,
                tag_filter: this.Common_Base.tags[2]
            }
        },
        activated() {
            console.log("---------activated  TAB2-----")

            if(this.temp_obj.length===0){
                this.temp_obj = this.Common_Base.getFilterDatas(this.tag_filter, this.Common_Base.data_alls)
            }
        },

        created() {
            console.log("---------created  TAB2-----")
            this.temp_obj = this.Common_Base.getFilterDatas(this.tag_filter, this.Common_Base.data_alls)
        },
        mounted() {
            console.log("---------mounted  TAB2-----")

        },
        methods: {}
    }
</script>

<style scoped>

</style>



