<template>
  <div class="main">
    <div class="close">
      <i class="el-icon-close" @click="deletePic"/>
    </div>
    <el-dropdown @command="handleCommand">
  <span class="el-dropdown-link">
    添加标签<i class="el-icon-arrow-down el-icon--right"></i>
  </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item v-for="(data,index) in tags" :key="index" v-bind:command="data">{{data}}</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>

    <div class="tag_main">
      <el-tag
        v-for="tag in obj.tags0"
        :key="tag"
        closable
        :disable-transitions="false"
        @close="handleClose(tag)"
        :type="type"
      >
        {{tag}}
      </el-tag>
    </div>

    <el-row :gutter="10">
      <div v-for="(data, index) in obj.urls" :key="index">
        <el-col :span="4">
          <el-card :body-style="{ padding: '5px' }">
            <div class="image_main">
              <img v-bind:src="data" class="image">
            </div>

          </el-card>
        </el-col>
      </div>
    </el-row>

    <!--<div  class="image_main">-->
    <!--<div v-for="(data, index) in obj.urls" :key="index">-->
    <!--<img v-bind:src="data" class="image">-->

    <!--</div>-->
    <!--</div>-->


    <el-input style="margin-left:1rem" v-model="obj.description"></el-input>

  </div>
</template>

<script>

    export default {
        name: "PicDataMotify",
        props: {
            obj: Object,
        },
        data() {
            return {
                tags: this.Common_Base.tags,
                type:'danger'
            }
        },
        mounted() {
            // console.log(this.obj)


        },
        methods: {
            deletePic() {
                console.log('deletePic')
                this.$emit('click_base', this.obj);
            },
            handleCommand(command) {
                this.$emit('click_tag_add', this.obj,command);
            },
            handleClose(tag) {
                this.$emit('click_tag_remove', this.obj,tag);
            },
        }
    }
</script>

<style lang="stylus" scoped>

  .tag_main {
    width 100%
    flex-direction row
    display flex
    padding 10px
  }
    .el-tag{
      margin-left 10px
    }

  .main
    display flex;
    width 100%

    background #bfcbd9
    flex-direction column

    .el-row
      width 100%
      height 90%
      margin-top 10px
      padding 5px

    .image_main
      display: flex;
      height: 100%;

      flex-direction: row;
      align-items: center;
      justify-content: center;

    .image {
      width: 8rem
      height: 8rem
    }

    .close
      flex 1
      align-self flex-end
      display flex
      flex-direction column
      justify-content flex-start
      padding 0.45rem

  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }

  .el-icon-arrow-down {
    font-size: 12px;
  }

  div div img {
    cursor: pointer;
    transition: all 0.6s;
  }


  div i:hover {
    transform: scale(1.8);
  }

</style>



